.nav {
	position: fixed;
	top: -100%;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.9);
	opacity: 1;

	display: none;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	transition: opacity 0.2s ease-in, top 0.2s linear;
	text-align: center;
	color: #ffffff;
}

.nav--active {
	z-index: 99;
	display: flex;
	top: 0;
	opacity: 1;
	transition: top 0.2s linear, opacity 0.2s ease-in;
}

.nav__top {
	margin-bottom: 50px;
	font-weight: 700;
	font-size: 18px;
	line-height: 1;
	letter-spacing: 4px;
	text-transform: uppercase;

	color: $accentText;

	/* Mobile XS */
	@media (max-width: 599px) {
		font-size: 16px;
		margin-bottom: 30px;
	}
}

.nav__list {
	font-weight: 700;
	font-size: 24px;
	line-height: 1;

	/* Mobile XS */
	@media (max-width: 599px) {
		font-size: 18px;
	}

	li:not(:last-child) {
		margin-bottom: 40px;

		/* Mobile XS */
		@media (max-width: 599px) {
			margin-bottom: 30px;
		}
	}

	a {
		text-decoration: none;
		color: #ffffff;
		transition: color 0.2s ease-in;

		&:hover {
			color: $accentText;
		}
	}
}
