.button-solid {
	height: 60px;
	padding-left: 40px;
	padding-right: 40px;
	border-radius: 30px;
	border: none;

	background: #ffffff;
	border: 1px solid rgba(255, 255, 255, 0.2);

	font-weight: 800;
	font-size: 14px;
	line-height: 60px;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #2e2e2e;

	cursor: pointer;
	transition: 0.5s;

	&:hover {
		background: #000000;
		color: #ffffff;
	}

	&:focus {
		outline: none;
		background: rgb(48, 48, 48);
		color: #f2f2f2;
	}
}
