.input {
	display: block;
	height: 60px;
	padding-left: 30px;
	padding-right: 30px;

	background: rgba(0, 0, 0, 0.75);
	border-radius: 30px;
	border: none;

	font-weight: 700;
	font-size: 14px;
	line-height: 60px;
	color: #ffffff;

	/* Mobile */
	@media (max-width: 899px) {
		padding-left: 46px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		padding-left: 36px;
	}
}

.input::placeholder {
	color: #ffffff;
}

.input:focus {
	background: rgb(0, 0, 0);
	border: 1px solid #00c470;
	outline: none;
}
