.header {
	position: relative;
	z-index: 99;

	@include bgImage("./../img/bg/header.jpg");

	&::before {
		@extend %overlay;
	}

	/* Desktop 1600 */
	@media (max-width: 1600px) {
		@include bgImage("./../img/bg/header-1600.jpg");
	}

	/* Tablet */
	@media (max-width: 1199px) {
		padding-top: 40px;
		padding-bottom: 40px;

		@include bgImage("./../img/bg/header-900.jpg");
	}

	/* Mobile */
	@media (max-width: 899px) {
		@include bgImage("./../img/bg/header-600.jpg");
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		@include bgImage("./../img/bg/header-320.jpg");
	}
}

.header__wrapper {
	position: relative;
	z-index: 9;

	display: flex;
	flex-direction: column;
	align-items: center;

	padding-top: 60px;
	padding-bottom: 100px;
	color: #fff;

	/* Tablet */
	@media (max-width: 1199px) {
		padding-bottom: 10px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		padding-top: 20px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		padding-bottom: 4px;
	}
}

.header__buttons {
	position: relative;
	display: flex;
	justify-content: space-between;

	width: 1200px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;

	margin-bottom: 140px;

	/* Tablet */
	@media (max-width: 1199px) {
		width: 100%;
		margin-bottom: 100px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		margin-bottom: 30px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
	}
}

.header__buttons-naw-icon {
	flex-shrink: 0;
}

.header__buttons-search {
	margin-left: auto;
}

.header__content {
	margin-bottom: 148px;
	text-align: center;

	/* Tablet */
	@media (max-width: 1199px) {
		margin-bottom: 129px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		margin-bottom: 80px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		margin-bottom: 50px;
	}
}

.header__title {
	margin-bottom: 40px;

	font-weight: 700;
	font-size: 96px;
	line-height: 1;

	/* Tablet */
	@media (max-width: 1199px) {
		margin-bottom: 35px;
		font-size: 84px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		margin-bottom: 30px;
		font-size: 70px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
	}
}

.header__desc {
	margin-bottom: 70px;
	max-width: 730px;
	margin-left: auto;
	margin-right: auto;

	font-weight: 700;
	font-size: 20px;

	p {
		margin-bottom: 15px;
	}

	/* Tablet */
	@media (max-width: 1199px) {
		margin-bottom: 50px;
		max-width: 730px;
		font-size: 18px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		max-width: 430px;
		font-size: 16px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		margin-bottom: 30px;
	}
}

.header__form {
	display: flex;
	justify-content: center;
	margin-left: auto;
	margin-right: auto;

	/* Mobile */
	@media (max-width: 899px) {
		width: 370px;
		flex-direction: column;
		align-items: stretch;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		width: 100%;
	}
}

.header__form > * + * {
	margin-left: 20px;

	/* Mobile */
	@media (max-width: 899px) {
		margin-left: 0;
		margin-top: 20px;
	}
}

.header__form-input {
	width: 240px;

	/* Mobile */
	@media (max-width: 899px) {
		width: 100%;
	}
}
