.search {
	position: relative;
	margin-left: auto;
	z-index: 9;
}

.search__input {
	display: none;
	width: 0px;
	padding-left: 30px;
	padding-right: 70px;
	height: 60px;
	padding-left: 30px;
	padding-right: 30px;

	background: #ffffff;
	border-radius: 30px;
	border: none;

	font-weight: 500;
	font-size: 16px;
	line-height: 60px;
	color: #000000;

	transition: width 0.2s ease-in;

	&::placeholder {
		color: rgba(46, 46, 46, 0.5);
	}

	&:focus {
		outline: none;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		width: 220px;
		padding-left: 20px;
		font-size: 14px;
	}
}

.search__button {
	position: absolute;
	top: 0;
	right: 0;

	display: flex;
	justify-content: center;
	align-items: center;
	height: 60px;
	width: 60px;
	border-radius: 50%;
	background-color: #fff;
	border: none;
	cursor: pointer;

	transition: background-color 0.2s ease-in;

	&:hover {
		background: #00c470;

		.search__button-icon {
			stroke: rgb(255, 255, 255);
		}
	}

	&:focus {
		background-color: #00c470;
		outline: none;
	}
}

.search__button-icon {
	stroke: black;
	transition: stroke 0.2s ease-in;
}

// Активное состояние формы поиска

.search--visible {
	.search__input--visible {
		display: block;
		width: 340px;
	}
	.search__button {
		background: #00c470;

		.search__button-icon {
			stroke: #fff;
		}

		&:hover {
			background: #15d784;
		}
	}
}
