.package {
	display: flex;
	flex-direction: column;
	padding-top: 40px;
	padding-bottom: 40px;

	background: rgba(0, 0, 0, 0.75);
	border-radius: 8px;

	text-align: center;
	color: #ffffff;
}

.package--special {
	background: rgba(48, 79, 254, 0.95);

	& .package__title {
		color: #fff;
	}

	& .package__period {
		background: rgba(0, 0, 0, 0.1);
	}
}

.package__header {
	display: flex;
	flex-direction: column;
	align-items: center;

	padding-left: 15px;
	padding-right: 15px;
	padding-bottom: 40px;
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.package__title {
	margin-bottom: 20px;

	font-weight: 700;
	font-size: 16px;
	line-height: 1;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #b2b2b2;
}

.package__price {
	margin-bottom: 15px;
	font-weight: 700;
	font-size: 80px;
	line-height: 1;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #ffffff;
}

.package__period {
	display: inline-block;
	padding-left: 15px;
	padding-right: 15px;
	height: 24px;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 30px;

	font-weight: 700;
	font-size: 12px;
	line-height: 24px;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #ffffff;
}

.package__list {
	padding-top: 40px;
	padding-left: 15px;
	padding-right: 15px;
	margin-bottom: 58px;

	/* Mobile */
	@media (max-width: 899px) {
		margin-bottom: 56px;
	}
}

.package__list li {
	margin-bottom: 25px;

	font-weight: 500;
	font-size: 16px;
	line-height: 1;

	&:last-child {
		margin-bottom: 0;
	}
}

.package__button {
	margin-top: auto;
}
