.button {
	display: inline-block;
	height: 60px;
	padding-left: 40px;
	padding-right: 40px;
	background: #00c470;
	border-radius: 30px;
	border: none;

	font-weight: 800;
	font-size: 14px;
	line-height: 60px;
	letter-spacing: 4px;
	text-transform: uppercase;
	text-decoration: none;

	color: #ffffff;
	cursor: pointer;
	transition: 0.5s;

	&:hover {
		background: #00b667;
	}

	&:focus {
		outline: none;
		background: #007441;
	}
}
