.button-outline {
	height: 60px;
	padding-left: 40px;
	padding-right: 40px;
	border-radius: 30px;
	border: none;

	background: transparent;
	border: 1px solid rgba(255, 255, 255, 0.2);

	font-weight: 800;
	font-size: 14px;
	line-height: 60px;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #ffffff;

	cursor: pointer;
	transition: 0.5s;

	&:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(255, 255, 255, 0);
	}

	&:focus {
		outline: none;
		background: rgb(0, 0, 0);
	}
}
