.footer {
	padding-top: 70px;
	padding-bottom: 70px;
	background: #1a1a1a;
	color: #fff;

	/* Tablet */
	@media (max-width: 1199px) {
		padding-top: 60px;
		padding-bottom: 60px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		padding-top: 40px;
		padding-bottom: 40px;
	}
}

.footer__row {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 46px;

	/* Tablet */
	@media (max-width: 1199px) {
		margin-bottom: 36px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		flex-direction: column;
		margin-bottom: 24px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		margin-bottom: 40px;
	}
}

.footer__nav {
	margin-right: 30px;

	/* Tablet */
	@media (max-width: 1199px) {
		margin-right: 30px;
	}

	/* Mobile */
	@media (max-width: 899px) {
		margin-right: 0;
		margin-bottom: 27px;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 40px;
	}
}

.footer__nav-item {
	margin-right: 50px;
	font-weight: 700;
	font-size: 16px;
	line-height: 1;
	letter-spacing: 4px;
	text-transform: uppercase;
	color: #ffffff;
	text-decoration: none;
	transition: 0.5s;

	&:last-child {
		margin-right: 0;
	}

	&:hover {
		color: #15d784;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		margin-right: 0;
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.footer__social {
	display: flex;
}

.footer__social-item {
	display: flex;
	justify-content: center;
	align-items: center;

	margin-right: 30px;

	width: 48px;
	height: 48px;
	background: rgba(196, 196, 196, 0.1);
	border-radius: 50%;
	transition: 0.5s;

	&:last-child {
		margin-right: 0;
	}

	&:hover {
		background: #00c470;
	}

	&:focus {
		outline: none;
	}

	/* Mobile XS */
	@media (max-width: 599px) {
		margin-right: 20px;
	}
}

.footer__copyright {
	font-weight: 500;
	font-size: 14px;
	line-height: 1.5;
	text-align: center;
	color: rgba(255, 255, 255, 0.3);

	& span {
		text-transform: uppercase;
		font-weight: 700;
	}
}

.footer__copyright p {
	/* Mobile XS */
	@media (max-width: 599px) {
		width: 200px;
		margin-left: auto;
		margin-right: auto;
	}
}

.footer__copyright p + p {
	margin-top: 15px;
}
