.button-round {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 60px;
	width: 60px;
	border-radius: 50%;
	background-color: #fff;
	border: none;
	cursor: pointer;
	transition: 0.5s;

	&:focus {
		outline: none;
	}
}

.button-round:hover {
	background: #00c470;
}
